<template>
  <section class="card-stack-wrapper">
      <base-card flipped class="card-stack"
      
      v-for="card in cards"
      :key="`${card.value} ${card.suit}`"
      :card="card"
      />
  </section>
</template>

<script>
import BaseCard from "@/components/BaseCard.vue";
export default {
props:{
    cards: Array
},
components:{
    BaseCard
},

}
</script>

<style >
    section{
        margin: 0 2rem;
        /* position: relative; */
    }

    .card-stack-wrapper{
        display: grid;
        place-items: center;
    }
    .card-stack{
        grid-row: 1;
        grid-column: 1;
        position: relative;
        z-index: 1;
        left: 2rem;
    }



    .card-stack:nth-child(2){
        transform: rotate(4deg);
    }
    .card-stack:nth-child(3){
        transform: rotate(-4deg);
    }

    @media only screen and (max-width: 680px) {

        .card-stack{
            left:0px;
        }
    }
</style>