<template>
    <div class="base-card-wrapper">
        <article class="card" v-if="!flipped">
            <div class="top-left">
                <img class="icon" :src="imageSource" alt="">
                <p>{{displayValue}}</p>
            </div>
            <div class="center">
                <img class="icon large" :src="imageSource" alt="">
            </div>
            <div class="bottom-right">
                <img class="icon" :src="imageSource" alt="">
                <p>{{displayValue}}</p>
            </div>
        </article>

        <article v-else class="card flipped">

        </article>

    </div>
</template>

<script>
export default {
props:{
    card: Object,
    flipped: Boolean
},
computed:{
    imageSource(){
        return require(`@/assets/${this.card.suit}.svg`)
    },
    displayValue(){
        switch (this.card.value) {
            case 11:
                return 'J'
            case 12:
                return 'Q'
            case 13:
                return 'K'
            case 14:
                return 'A'

            default:
                return this.card.value
            
        }
    }
}
}
</script>
<style lang="scss">
// .base-card-wrapper{
//     // max-width: 200px;
//     // height: fit-content;
// }
.card{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    background-color: lightgray;
    height: 28rem;
    width: 18rem;
    padding: 1rem;
    border: 10px #EEE solid;
    border-radius: 1rem;
    font-size: 2rem;
    &.flipped{
        background: url("~@/assets/dots.svg");
        background-color: rgb(228, 197, 157);
    }

    .top-left{
        align-self: flex-start;
        display: flex;
        flex-direction: column;
        align-items: center;
    }

    .bottom-right{
        align-self: flex-end;
        display: flex;
        flex-direction: column;
        align-items: center;
        transform: rotate(180deg);
    }

    .icon{
        height: 2rem;
        width: 2rem;

    &.large{
        height: 3rem;
        width: 3rem;
    }
    }

}

@media only screen and (max-width: 680px) {
  .card {
    // transform: scale(0.6);
    height: 200px;
    width: 130px;
    font-size: 1.5rem;
    padding: 0.4rem;
    border: 4px #EEE solid;
    .icon{
        height: 1rem;
        width: 1rem;
    &.large{
        height: 2rem;
        width: 2rem;
    }
    }
  }
  
}
@media only screen and (max-width: 340px) {
  .card {
    // transform: scale(0.6);
    height: 180px;
    width: 120px;
 
  }
  
}



</style>