function createDeck() {
    let suitsArr = ['clubs','diamonds','hearts','spades', ]
    let cardDeck =[]
    suitsArr.forEach(suit =>{
        for (let i = 2; i <= 14; i++) {
            cardDeck.push({suit, value : i})
        }
    })
    
    return cardDeck
}

function shuffleArray(array) {
    for (let i = array.length - 1; i > 0; i--) {
      let j = Math.floor(Math.random() * (i + 1));
      let temp = array[i];
      array[i] = array[j];
      array[j] = temp;
    }
    return array
  }

export {createDeck, shuffleArray}