import Vue from 'vue'
import App from './App.vue'
import * as Card from '@/game'

Vue.config.productionTip = false

new Vue({
  data(){
    return{
      cards : Card.shuffleArray(Card.createDeck()),
      emptyStack: [],
      points: 0,
      attempts: 5,
      showGameOver: false,
      highScore: window.localStorage.getItem('high-score') || 0

    }
  },

  created(){
    // console.log(this.cards)
    this.emptyStack.push(this.cards.pop())
    // console.log(this.emptyStack)
  },
  methods:{
    resetValues(){
      this.cards = Card.shuffleArray(Card.createDeck())
      this.emptyStack = []
      this.emptyStack.push(this.cards.pop())
      this.points = 0
      this.attempts = 5
      this.showGameOver = false
    },
    moveCard(e){
      let correct = false
      if (this.cards.length > 0 && this.showGameOver == false) {
        let firstValue = this.emptyStack[this.emptyStack.length - 1].value
        this.emptyStack.push(this.cards.pop())
        let secondValue = this.emptyStack[this.emptyStack.length - 1].value

        if (e == 'equal') {
          if (firstValue == secondValue) {
            correct = true
            // console.log(correct)
          }
        }
        else if(e == 'lower'){
          if  (firstValue > secondValue) {
            correct = true
            // console.log(correct)
          }
        }
        else{
          if (firstValue < secondValue) {
            correct = true
            // console.log(correct)
          }
        }
        if (correct) {
          this.points++
        }else{
          this.attempts--
          if (this.attempts == 0) {
            if (this.points > this.highScore) {
              window.localStorage.setItem('high-score', JSON.stringify(this.points))
              this.highScore = window.localStorage.getItem('high-score')
            }
            this.showGameOver = true
          }
        }

        
      }


    }
  },
  render: h => h(App)
}).$mount('#app')
